<template>
  <div class="top" id="moneyDetailsBox">
    <div
      v-if="ownerList.length > 0"
      class="scroll"
    >
      <mt-loadmore
        :top-method="loadTop"
        :bottom-method="loadBottom"
        :bottom-all-loaded="allLoaded"
        ref="loadmore"
        :autoFill="isAutoFill"
      >
        <div
          v-for="(item, i) in ownerList"
          :key="i"
          class="content"
        >
          <div class="per_info">
            <p style="color:#FF7D41;">
              <span v-if="item.order.roomUser">{{ item.order.roomUser.replace(/.(?=.)/g, '*') }}</span>
              <span v-else>{{ item.sysUser?item.sysUser.realName.replace(/.(?=.)/g, '*'):'' }}</span>
              <span
                style="color:#707070"
                v-if="item.order.phone == null || item.order.phone == '' "
              >{{ item.sysUser == null?'':item.sysUser.phone.replace(/(\d{5})\d{6}(\d{0})/, '$1$2******') }}</span>
              <span
                style="color:#707070;"
                v-else
              >{{ item.order.phone.replace(/(\d{5})\d{6}(\d{0})/, '$1$2******') }}</span>
              <span
                class="my-invoice"
                v-if="item.isInvoice == 1"
              >开发票</span>
            </p>
            <p
              style="color:#707070"
              v-if="item.order && item.order.accountUnitId > 0"
            >
              <span>挂账单位</span>
            </p>
            <p
              style="color:#707070"
              v-else
            >
              {{ orderSourceMap[item.order.orderSource] }}
              <span>{{ item.order.externalOrderSn!=null? item.order.externalOrderSn:'' }}</span>
            </p>
          </div>
          <div style="display:flex;justify-content: space-between;">
            <div style="text-align:left;line-height:30px;width:70%;width:100%;">
              <p style="color:#707070;margin-top:8px;display:flex;flex-direction:column;">
                <span>{{ item.roomScale?item.roomScale.scaleName:'' }}-{{ item.virtualRoomScale?item.virtualRoomScale.scaleName:'' }}</span>
                <span>
                  共1间
                  <span v-if="item.roomSn==null">未办理入住</span>
                  <span v-else>房间号{{ item.roomSn }}
                    <img
                      v-if="item.status == '1'"
                      class="noLock"
                      src="@/assets/order/icon_close.png"
                    >
                  </span>
                  <span
                    v-if="item.orderType==3 ||item.orderType==4"
                    class="long"
                  >{{ item.orderType==3?'长':'短' }}租</span>
                </span>
              </p>
              <p v-if="item.orderType==3">租期：{{item.order.startTime | dataFilter }} 至 {{item.order.endTime | dataFilter }}</p>
              <p v-if="item.orderType==3">共{{((item.order.endTime.substr(0,4))-(item.order.startTime.substr(0,4)))*12+((item.order.endTime.substr(5,2)-item.order.startTime.substr(5,2)))}}月</p>
              <p v-if="item.orderType==3">
                <span>&yen;{{item.order.price?item.order.price:'0'}}/月</span>
              </p>
              <p
                style="color:#000;"
                v-if="item.orderType!=3"
              >{{item.order.startTime | dataFilter('MM-DD') }}至{{item.order.endTime | dataFilter('MM-DD') }}&emsp;&emsp;共{{ item.order.diffDay }}{{item.orderType==2?'小时':'晚'}}</p>
              <p style="color:#707070; font-size:14px;">备注： {{ item.isCon == 0?'无':'续住' }}</p>
            </div>
            <div
              class="info_right"
              style="display:flex;flex-direction: column;line-height:50px;"
            >
              <span style="display:inline-block;height:20px;color:#707070;">{{ item.statusFont }}</span>
            </div>
          </div>
        </div>
      </mt-loadmore>
    </div>
    <div v-else>暂无数据</div>
    <!-- 结束 -->
  </div>
</template>
<script>
import '@/assets/styles/global.css';
import { GetSystemDictDataByType } from '@/api/api_query';
import { GetBusinessOrderroomHistoryByroom } from '@/api/api_business_hotel_and_scale';
export default {
    data() {
        return {
            allLoaded: false, // 可以进行上拉
            isAutoFill: false, // 是否自动触发上拉函数
            ownerList: [], //列表数据
            currentInfo: [],
            orderSourceMap: {},
            payment: '',
            paySource: '',
            dataForm: {
                pageSize: 10,
                pageNum: 0,
                roomId: this.$route.query.roomId
            }
        };
    },
    created() {
        this.$store.commit('setHeader', '结算订单');
        this.getType();
    },
    methods: {
        getType() {
            GetSystemDictDataByType({ dictType: 'business_order_source' }).done((res) => {
                let obj = {};
                for (var i = 0; i < res.data.length; i++) {
                    obj[res.data[i].dictValue] = res.data[i].dictLabel;
                }
                this.orderSourceMap = obj;
                this.orderPageList();
            });
        },
        // 下拉刷新
        loadTop() {
            this.ownerList = [];
            this.dataForm.pageNum = 0;
            this.orderPageList();
        },
        // 上拉加载
        loadBottom() {
            this.orderPageList();
        },
        //  上滑加载列表
        orderPageList() {
            GetBusinessOrderroomHistoryByroom(this.dataForm).done((res) => {
                this.ownerList = this.ownerList.concat(res.data);
                if (res.total > this.ownerList.length) {
                    this.showTop = true;
                    this.showButtom = true;
                    this.allLoaded = false;
                } else if (res.total == this.ownerList.length) {
                    this.showTop = false;
                    this.showButtom = true;
                    this.allLoaded = true;
                }
            });
            this.dataForm.pageNum++;
        }
    }
};
</script>
<style scoped>
#moneyDetailsBox {
    position: fixed;
    top: 50px;
    bottom: 0px;
    left: 0px;
    right: 0px;
    overflow-y: scroll;
}
.long {
    margin: 8px 10px;
    background: #fff;
    border-radius: 5px;
    text-align: left;
    padding: 0 10px;
    line-height: 30px;
}
.long > p {
    display: flex;
    justify-content: space-between;
}
.content {
    margin: 10px;
    background: #fff;
    padding: 10px;
    border-radius: 10px;
}
</style>



